// Typography

.section-header {
  h2 {
    text-align: center;
    font-family: $deff;
    font-size: 44px;
    font-weight: 700;

    background: $accent-gradient;
    background-clip: text;
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent; } }

::selection {}

ul {
  margin: 0;
  padding: 0;
  list-style-type: none; }

a {
  transition: all .3s ease;
  text-decoration: none;
  &:focus {
    text-decoration: none;
    outline: none; } }

img {
  width: auto;
  max-width: 100%;
  height: auto; }

